import React, { useEffect, useContext, useState } from 'react';
import posed from 'react-pose';
import { MorphingIcon } from '../../components';
import { SocketContext } from '../../context/Socket';
import { useTitle, useKeyDown } from '../../hooks';
import './Signals.scss';
const secuency = [
    { icon: 'weight', description: 'Magnitud' },
    { icon: 'sensor', description: 'Sensado' },
    { icon: 'signal', description: 'Señal eléctrica' },
    { icon: 'cpu', description: 'Conversión' },
    { icon: 'message', description: 'Comunicación' },
    { icon: 'computer', description: 'Procesamiento' },
    { icon: 'plot', description: 'Lectura' },
];

const Mobile = posed.div({
    active: { x: ({ x }) => `${x}%`, transition: { ease: 'linear', duration: 240 } },
});
const Speed: React.FC = () => {
    const { updateTitle } = useTitle();
    const { socket } = useContext(SocketContext);
    const [index, setIndex] = useState(0);
    const x = (80 / secuency.length) * (index + 1);
    const iterate = () => {
        setIndex((index: number) => (index >= secuency.length - 1 ? 0 : index + 1));
    };
    useKeyDown({
        KeyI: iterate,
        Space: iterate,
    });
    useEffect(() => {
        updateTitle('Procesamiento de una señal');
    }, [updateTitle]);
    useEffect(() => {
        socket.on('iterate', iterate);
    }, [socket]);

    return (
        <div className="slide">
            <span className="line left top" />
            <Mobile poseKey={x} pose={'active'} className="mobile" x={x}>
                <MorphingIcon width={256} height={256} icon={secuency[index].icon} />
                <p className="description">{secuency[index].description}</p>
            </Mobile>
            <span className="way" />
            <span className="line right bottom" />
        </div>
    );
};

export default Speed;
